// Logique de scoring spcifique  Azul : Summer Pavilion
import { GameState, TileColor } from '../../models/types';

export function calculateSummerPavilionScore(gameState: GameState): number {
  // Calculer la somme des scores de tous les joueurs
  return gameState.players.reduce((totalScore, player) => {
    // Points de base du joueur
    let playerScore = player.board.score;
    
    // Dans Summer Pavilion, le scoring se fait diffremment
    // Les bonus sont obtenus pendant le jeu, pas  la fin
    // Ici on retourne juste le score actuel du joueur
    
    return totalScore + playerScore;
  }, 0);
}

// Vrifie si une toile est complte (toutes les 6 positions remplies)
function isStarComplete(placedTiles: { color: TileColor; flower: number; pos: number }[], flowerIndex: number): boolean {
  const tilesOnFlower = placedTiles.filter(t => t.flower === flowerIndex);
  return tilesOnFlower.length === 6; // 6 positions par toile
}

// Vrifie si tous les espaces d'un mme cot sont remplis
function allCostSpacesFilled(placedTiles: { color: TileColor; flower: number; pos: number }[], cost: number): boolean {
  // Dans Summer Pavilion, chaque toile a une position de chaque cot (1  6)
  // On vrifie que pour chaque fleur, la position correspondant au cot est remplie
  const flowers = [0, 1, 2, 3, 4, 5]; // Les 6 fleurs
  
  // Chaque position dans une fleur a un cot spcifique
  // La position cost-1 correspond au cot 'cost'
  const costPosition = cost - 1;
  
  // Vrifie si chaque fleur a une tuile  la position du cot spcifi
  return flowers.every(flower => 
    placedTiles.some(tile => tile.flower === flower && tile.pos === costPosition)
  );
}

// Vrifie si on doit obtenir un bonus pilier (4 espaces adjacents  un pilier remplis)
function checkPillarBonus(placedTiles: { color: TileColor; flower: number; pos: number }[], newTile: { flower: number; pos: number }): boolean {
  // Les piliers sont situs entre la fleur centrale (6) et les fleurs extrieures (0-5)
  // Il y a 6 piliers, un entre chaque fleur extrieure et la centrale
  
  // Pour chaque pilier (position entre fleur extrieure et centrale)
  for (let pillarIndex = 0; pillarIndex < 6; pillarIndex++) {
    const outerFlower = pillarIndex; // Fleur extrieure
    const centerFlower = 6; // Fleur centrale
    
    // Les 4 espaces adjacents au pilier sont :
    // - 2 espaces sur la fleur extrieure (positions qui touchent le centre)
    // - 2 espaces sur la fleur centrale (positions qui touchent cette fleur extrieure)
    
    const adjacentSpaces = [
      { flower: outerFlower, pos: 0 }, // Position vers le centre sur fleur extrieure
      { flower: outerFlower, pos: 1 }, // Position adjacente sur fleur extrieure
      { flower: centerFlower, pos: pillarIndex }, // Position correspondante sur fleur centrale
      { flower: centerFlower, pos: (pillarIndex + 1) % 6 } // Position adjacente sur fleur centrale
    ];
    
    // Vrifier si tous les espaces adjacents sont remplis aprs ce placement
    const allFilled = adjacentSpaces.every(space => {
      return placedTiles.some(tile => tile.flower === space.flower && tile.pos === space.pos) ||
             (newTile.flower === space.flower && newTile.pos === space.pos);
    });
    
    if (allFilled) {
      // Vrifier que ce bonus n'a pas dj t obtenu
      const wasAlreadyComplete = adjacentSpaces.every(space => {
        return placedTiles.some(tile => tile.flower === space.flower && tile.pos === space.pos);
      });
      
      if (!wasAlreadyComplete) {
        return true; // Nouveau bonus pilier obtenu !
      }
    }
  }
  
  return false;
}

// Vrifie si on doit obtenir un bonus statue (4 espaces adjacents  une statue remplis)
function checkStatueBonus(placedTiles: { color: TileColor; flower: number; pos: number }[], newTile: { flower: number; pos: number }): boolean {
  // Les statues sont situes entre les fleurs extrieures adjacentes
  // Il y a 6 statues, une entre chaque paire de fleurs extrieures adjacentes
  
  for (let statueIndex = 0; statueIndex < 6; statueIndex++) {
    const flower1 = statueIndex;
    const flower2 = (statueIndex + 1) % 6; // Fleur adjacente
    
    // Les 4 espaces adjacents  la statue sont :
    // - 2 espaces sur la premire fleur (cts qui touchent la statue)
    // - 2 espaces sur la deuxime fleur (cts qui touchent la statue)
    
    const adjacentSpaces = [
      { flower: flower1, pos: 5 }, // Ct droit de flower1
      { flower: flower1, pos: 4 }, // Position adjacente sur flower1
      { flower: flower2, pos: 0 }, // Ct gauche de flower2
      { flower: flower2, pos: 1 }  // Position adjacente sur flower2
    ];
    
    // Vrifier si tous les espaces adjacents sont remplis aprs ce placement
    const allFilled = adjacentSpaces.every(space => {
      return placedTiles.some(tile => tile.flower === space.flower && tile.pos === space.pos) ||
             (newTile.flower === space.flower && newTile.pos === space.pos);
    });
    
    if (allFilled) {
      // Vrifier que ce bonus n'a pas dj t obtenu
      const wasAlreadyComplete = adjacentSpaces.every(space => {
        return placedTiles.some(tile => tile.flower === space.flower && tile.pos === space.pos);
      });
      
      if (!wasAlreadyComplete) {
        return true; // Nouveau bonus statue obtenu !
      }
    }
  }
  
  return false;
}

// Vrifie si on doit obtenir un bonus fentre (2 espaces adjacents  une fentre remplis)
function checkWindowBonus(placedTiles: { color: TileColor; flower: number; pos: number }[], newTile: { flower: number; pos: number }): boolean {
  // Les fentres sont situes aux extrmits des fleurs extrieures
  // Il y a 12 fentres, 2 par fleur extrieure (une  chaque extrmit)
  
  for (let flower = 0; flower < 6; flower++) {
    // Fentre 1 : extrmit "gauche" de la fleur (positions 2-3)
    const window1Spaces = [
      { flower: flower, pos: 2 },
      { flower: flower, pos: 3 }
    ];
    
    // Fentre 2 : extrmit "droite" de la fleur (positions 4-5)
    const window2Spaces = [
      { flower: flower, pos: 4 },
      { flower: flower, pos: 5 }
    ];
    
    // Vrifier chaque fentre
    for (const windowSpaces of [window1Spaces, window2Spaces]) {
      const allFilled = windowSpaces.every(space => {
        return placedTiles.some(tile => tile.flower === space.flower && tile.pos === space.pos) ||
               (newTile.flower === space.flower && newTile.pos === space.pos);
      });
      
      if (allFilled) {
        // Vrifier que ce bonus n'a pas dj t obtenu
        const wasAlreadyComplete = windowSpaces.every(space => {
          return placedTiles.some(tile => tile.flower === space.flower && tile.pos === space.pos);
        });
        
        if (!wasAlreadyComplete) {
          return true; // Nouveau bonus fentre obtenu !
        }
      }
    }
  }
  
  return false;
}

// Fonction pour vrifier tous les bonus lors du placement d'une tuile
export function checkBonusesOnPlacement(
  placedTiles: { color: TileColor; flower: number; pos: number }[], 
  newTile: { flower: number; pos: number }
): { 
  pillar: boolean; 
  statue: boolean; 
  window: boolean;
  bonusTiles: number; // Nombre total de tuiles bonus  rcuprer
} {
  const pillarBonus = checkPillarBonus(placedTiles, newTile);
  const statueBonus = checkStatueBonus(placedTiles, newTile);
  const windowBonus = checkWindowBonus(placedTiles, newTile);
  
  let bonusTiles = 0;
  if (pillarBonus) bonusTiles += 1; // 1 tuile pour pilier
  if (statueBonus) bonusTiles += 2;  // 2 tuiles pour statue
  if (windowBonus) bonusTiles += 3;  // 3 tuiles pour fentre
  
  return {
    pillar: pillarBonus,
    statue: statueBonus,
    window: windowBonus,
    bonusTiles
  };
}