import React, { useEffect, useState } from "react";
import Tile from "../Tile/Tile";
import { Tile as TileType, TileColor } from "../../models/types";
import "./AIAnimation.css";

/**
 * Props pour le composant d'animation des actions de l'IA
 * @interface AIAnimationProps
 */
interface AIAnimationProps {
  /** lment DOM source du mouvement (factory ou center) */
  sourceElement: HTMLElement | null;
  /** lment DOM cible du mouvement (patternLine ou floorLine) */
  targetElement: HTMLElement | null;
  /** Tuiles  animer */
  tiles: TileType[];
  /** Couleur des tuiles */
  color: TileColor;
  /** Type de cible ('patternLine' ou 'floorLine') */
  targetType: "patternLine" | "floorLine";
  /** Index de la ligne cible (pour les pattern lines) */
  targetIndex: number;
  /** Callback appel quand l'animation est termine */
  onAnimationComplete: () => void;
}

/**
 * Composant qui anime les mouvements de l'IA dans le jeu Azul.
 * Gre l'animation des tuiles d'une source (fabrique/centre) vers une destination (ligne).
 * 
 * @component
 */
const AIAnimation: React.FC<AIAnimationProps> = ({
  sourceElement,
  targetElement,
  tiles,
  color,
  targetType,
  targetIndex,
  onAnimationComplete,
}) => {
  // tats pour grer les positions et les phases de l'animation
  const [positions, setPositions] = useState<{
    startX: number;
    startY: number;
    endX: number;
    endY: number;
  } | null>(null);
  const [animationState, setAnimationState] = useState<"moving" | "landing" | "complete">("moving");
  const [showPath, setShowPath] = useState(false);

  /**
   * Calcule les positions de dpart et d'arrive pour l'animation
   * Ajuste la position d'arrive en fonction du type de cible
   */
  useEffect(() => {
    if (sourceElement && targetElement) {
      const sourceRect = sourceElement.getBoundingClientRect();
      const targetRect = targetElement.getBoundingClientRect();

      // Position de dpart (centre de la source)
      const startX = sourceRect.left + sourceRect.width / 2;
      const startY = sourceRect.top + sourceRect.height / 2;

      // Position d'arrive avec ajustements bass sur le type de cible
      let endX, endY;

      if (targetType === "patternLine") {
        // Pour les lignes de motif, calcule la position base sur les tuiles existantes
        const availableSpots = targetIndex + 1; // Les lignes ont 1-5 espaces
        const filledSpots = targetElement.querySelectorAll(".tile").length;

        // Calcule le dcalage pour le remplissage de droite  gauche
        const tileWidth = 28;
        const tileMargin = 4;
        const offsetX = (availableSpots - filledSpots - 1) * (tileWidth + tileMargin);

        // Positionne  droite de la ligne, moins le dcalage
        endX = targetRect.right - 20 - offsetX;
        endY = targetRect.top + targetRect.height / 2;
      } else {
        // Pour la ligne de pnalit, positionne en fonction des tuiles existantes
        const floorTiles = targetElement.querySelectorAll(".tile").length;
        const tileWidth = 28;
        const tileMargin = 5;

        // La ligne de pnalit se remplit de gauche  droite
        endX = targetRect.left + 20 + floorTiles * (tileWidth + tileMargin);
        endY = targetRect.top + targetRect.height / 2;
      }

      // Stocke les positions calcules
      setPositions({
        startX,
        startY,
        endX: endX - startX, // Calcule le dcalage pour la transformation CSS
        endY: endY - startY, // Calcule le dcalage pour la transformation CSS
      });
      
      // Affiche l'effet de chemin aprs un court dlai
      setTimeout(() => setShowPath(true), 100);
    }
  }, [sourceElement, targetElement, targetType, targetIndex]);

  /**
   * Gre la squence d'animation en plusieurs phases
   * 1. Moving: dplacement des tuiles de la source vers la cible
   * 2. Landing: effet d'impact  l'arrive
   * 3. Complete: fin de l'animation et callback
   */
  useEffect(() => {
    if (positions) {
      // Premire phase: dplacement
      const landingTimer = setTimeout(() => {
        setAnimationState("landing");
        setShowPath(false);
        
        // Deuxime phase: atterrissage avec impact
        const completeTimer = setTimeout(() => {
          setAnimationState("complete");
          
          // Finalement: complte l'animation
          setTimeout(onAnimationComplete, 300);
        }, 400);
        
        return () => clearTimeout(completeTimer);
      }, 600);
      
      return () => clearTimeout(landingTimer);
    }
  }, [positions, onAnimationComplete]);

  // Ne rend rien si les positions ne sont pas calcules
  if (!positions) return null;

  // Calcule l'angle pour le chemin de mouvement
  const angle = Math.atan2(positions.endY, positions.endX) * (180 / Math.PI);
  const distance = Math.sqrt(positions.endX * positions.endX + positions.endY * positions.endY);

  return (
    <div className="ai-animation-container">
      {/* Chemin anim du mouvement */}
      {showPath && (
        <div
          className="ai-move-path"
          style={{
            top: `${positions.startY}px`,
            left: `${positions.startX}px`,
            width: `${distance}px`,
            transform: `rotate(${angle}deg)`,
          }}
        />
      )}
      
      {/* Effet d'ondulation  la source */}
      {animationState === "moving" && (
        <div
          className="ai-ripple"
          style={{
            top: `${positions.startY}px`,
            left: `${positions.startX}px`,
          }}
        />
      )}
      
      {/* Tuiles animes */}
      {Array(Math.min(tiles.length, 5))
        .fill(0)
        .map((_, i) => (
          <div
            key={i}
            className={`ai-animated-tile ${animationState === "landing" ? "landing" : ""} 
                      ${animationState === "complete" ? "placed" : ""}`}
            style={
              {
                left: `${positions.startX}px`,
                top: `${positions.startY}px`,
                "--end-x": `${positions.endX}px`,
                "--end-y": `${positions.endY}px`,
                animationDelay: `${i * 80}ms`,
              } as React.CSSProperties
            }
          >
            <Tile color={color} />
          </div>
        ))}

      {/* Effet d'impact quand les tuiles atterrissent */}
      {animationState === "landing" && (
        <div
          className="tile-impact-effect"
          style={{
            left: `${positions.startX + positions.endX}px`,
            top: `${positions.startY + positions.endY}px`,
          }}
        />
      )}
      
      {/* Effet d'ondulation  la cible pendant l'atterrissage */}
      {animationState === "landing" && (
        <div
          className="ai-ripple"
          style={{
            top: `${positions.startY + positions.endY}px`,
            left: `${positions.startX + positions.endX}px`,
          }}
        />
      )}
    </div>
  );
};

export default AIAnimation;